import React, { ReactFragment } from 'react';
import classNames from 'classnames';
import { motion } from 'framer-motion';
import { scrollEffectAnim } from '../../utilities/animationVariants';

import './Manifesto.scss';


interface ManifestoProps {
  img: string,
  imgAlt: string,
  imgClass?: string,
  title: string,
  body: string | ReactFragment,
}

const Manifesto = (props: ManifestoProps) => {
  return (
    <motion.div
      initial="offscreen"
      whileInView="onscreen"
      viewport={{ once: true, amount: 0.5 }}
      className="col-12 col-sm-6 col-lg-4 px-3 mb-4"
    >
      <motion.div variants={scrollEffectAnim} className="manifesto__image-wrapper mb-4">
        <img src={props.img} className={classNames(props.imgClass, "manifesto__image mx-auto")} alt={props.imgAlt} tabIndex={-1} />
      </motion.div>
      <motion.h4 variants={scrollEffectAnim} className="text-primary fw-bold mb-3">
        {props.title}
      </motion.h4>
      <motion.p variants={scrollEffectAnim} className="manifesto__body">
        {props.body}
      </motion.p>
    </motion.div>
  )
}

export default Manifesto;